$green: #255D38;

header {
  text-align: left;
  min-height: 3em;
  background-color: $green;
  color: white;
  z-index: 1000;
  * {
    background-color: $green; }
  h1 {
    font-size: 1.5em;
    line-height: 2em;
    margin: 0;
    display: inline-block;
    img {
      height: 1.9em;
      vertical-align: top;
      margin-right: 1em; } }
  a {
    display: inline-block;
    vertical-align: top;
    &.signout {
 }      // float: right
    &.help {
      // float: right
      font-weight: bold;
      font-size: 2em;
      padding: 0 0.5em;
      line-height: 1.5em;
      height: 1.5em; }
    &.add {
      margin-left: 1em; }
    i.fa, i.fas, i.fab {
      height: 2em;
      line-height: 2em;
      font-size: 1.5em; }
    text-decoration: none;
    color: white;
    line-height: 3em;
    padding: 0 1em;
    height: 3em;
    text-align: center;
    transition: background-color 0.4s;
    &:active, &:visited, &:hover {
      color: white; }
    &:hover {
      background-color: darken($green, 10%);
      color: white;
      text-decoration: none; }
    &::after {
      clear: both;
      display: block; } } }
.card {
  display: inline-block;
  width: 450px;
  margin: 10px;
  .card-title {
    font-weight: bold; }
  .card-body {
    padding: 0.5em; }
  color: white;
  select {
    float: right; }
  .xBtn {
    float: right;
    display: inline-block;
    width: 1.5em;
    height: 1.5em;
    background-color: #666;
    text-align: center;
    border-radius: 3px;
    color: white;
    text-decoration: none;
    &:visited {
      text-decoration: none; }
    &:hover {
      text-decoration: none;
      color: #000;
      background-color: white; } }
  .card-subtitle>div>div { // Autocomplete list
    z-index: 1000000; } }
input[type=text] {
  border: none; }
.difficulty {
  display: inline-block;
  width: 32%;
  margin: 0.5%;
  padding: 2%;
  box-sizing: border-box;

  border-radius: 3px;
  &.easy {
    background-color: #3d3; }
  &.medium {
    background-color: #3dd; }
  &.hard {
    background-color: #f77; }
  input[type=text] {
    background-color: transparent;
    width: 100%;
    text-align: center; }
  .btn-group {
    background-color: black;
    border-radius: 0.25em;
    button {
      padding: 0 0.48em 0.2em 0.48em;
      border: 0;
      margin-right: 0.1em; }
    button:last-of-type {
      margin-right: 0; }
    .active {
      background-color: black;
      color: white; } }
  button {
    padding: 0 0.35em 0.2em 0.35em;
    &.next {
      float: right; }
    &.back {
      float: left; } } }
h2 {
  margin-top: 1em; }
h3 {
  font-size: 1.2em;
  text-align: center; }
.removeBtn {
  font-weight: bold;
  border-radius: 0.3em;
  background-color: black;
  color: white;
  font-size: 0.7em;
  vertical-align: 0.1em;
  border: 0;
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  padding: 0;
  transition: background-color 0.2s;
  &+span {
    display: inline-block;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      display: block;
      width: 0%;
      height: 0.1em;
      margin-top: -0.7em;
      background: black;
      transition: all 0.5s cubic-bezier(.55, 0, .1, 1); } }
  &:hover {
    background-color: gray;
    &+span:after {
        width: 100%; } } }
.teamid {
  position: relative;
  span {
    font-size: 0.8em;
    top: 0.2em;
    display: none;
    position: absolute;
    z-index: 1000;
    white-space: nowrap;
    pointer-events: none;
    margin-left: 0.15em;
    padding: 0 0.4em;
    background-color: black;
    color: white; }
  outline: solid 0.1em black;
  padding: 0.1em 0.5em;
  &:hover {
    background-color: black;
    color: white; }
  &:hover span {
    display: inline-block;
    outline: solid 0.1em black; } }
#plusbtn {
  background-color: $green;
  color: white;
  font-size: 1.5em;
  position: fixed;
  right: 2em;
  bottom: 2em;
  width: 3em;
  height: 3em;
  border: 0;
  border-radius: 100em;
  box-shadow: 0 0.1em 0.3em rgba(0,0,0,0.12), 0 0.1em 0.2em rgba(0,0,0,0.24);
  transition: all 0.3s cubic-bezier(.25,.8,.25,1);
  &:hover {
    cursor: pointer;
    box-shadow: 0 0.7em 1.4em rgba(0,0,0,0.25), 0 0.5em 0.5em rgba(0,0,0,0.22); } }
#identifier, #identifier:focus {
  border: solid 1px gray;
  border-radius: 5px;
  width: 15em;
  color: white;
  background-color: black;
  &:valid {
    border: solid 1px white; } }
.scoreboxes {
  padding-top: 1em; }
.scorebox, .selectbox {
  display: inline-block;
  vertical-align: middle;
  padding-top: 0.1em;
  width: 2em;
  height: 2em;
  background-color: #ddd;
  border: solid 0.15em;
  color: white;
  box-sizing: border-box;
  margin: 0.1em; }
.scorebox {
  font-size: 2vw; }
.selectbox {
  font-size: 10vw;
  padding-top: 0.05em;
  border: solid 0.07em;
  transition: background-color 0.4s;
  .desc {
    font-size: 0.15em;
    display: block;
    color: black;
    transition: color 0.4s; }
  &:hover .desc {
    color: white; }
  &.easy:hover {
    background-color: #090; }
  &.medium:hover {
    background-color: #009; }
  &.hard:hover {
    background-color: #900; } }
.easy {
  border-color: #0f0;
  &.filled {
    background-color: #090; } }
.medium {
  border-color: #00f;
  &.filled {
    background-color: #009; } }
.hard {
  border-color: #f00;
  &.filled {
    background-color: #900; } }
#teamname {
  font-weight: bold; }
